#nft-desktop {
    width: -webkit-fill-available;

    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;

    color: #02073E;
}

#app-about {
    width: -webkit-fill-available;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;

    padding: 75px 20px;
}

#app-info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    user-select: none;
    padding: 15px;
    flex: 1;
}

#app-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
}

#app-title img {
    width: 100px;
}

#app-title span {
    font-size: 30px;
    font-family: Bree Serif;
    color: #02073E;
    line-height: 1.45;
    font-weight: 500;
    margin-right: 48px;
    -webkit-transition: 500ms;
    transition: 500ms;
    white-space: nowrap;
    margin: 10px 25px;
}

#app-features {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
}

#app-desc {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 500px;
    margin: 25px;
}

#app-features p,
#app-desc p,
#app-pricing .pricing-card span {
    font-size: 16px;
    font-family: Bree Serif;
    color: #02073E;
    line-height: 1.45;
    font-weight: 500;
    margin-right: 48px;
    -webkit-transition: 500ms;
    transition: 500ms;
    margin: 10px 25px;
    user-select: none;
}

#app-features p {
    cursor: pointer;
    text-align: center;
}

#app-features p.active {
    color: #2596be;
}

#app-desc p {
    font-size: 18px;
    text-align: center;
}

#app-distribution {
    background: #ffff;

    width: -webkit-fill-available;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    padding: 80px 20px;

    min-height: 500px;
}

#app-download > span {
    user-select: none;
}

#app-pricing {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

#app-pricing .pricing-card {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;

    border-radius: 7px;
    background-color: #F9FBFD;
    border: 1px solid #2596be;

    margin: 10px;
    padding: 20px;

    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;

    min-height: 300px;

    cursor: pointer;
}

#app-pricing .pricing-card:hover {
    transform: scale(1.05);
}

#app-pricing .pricing-card .pricing-title {
    font-size: 20px;
    user-select: none;
}

#app-pricing .pricing-card .pricing-value {
    font-size: 30px;
    user-select: none;
}

#app-download {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#checkout-modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 15px 100px;
}

#checkout-modal span {
    margin: 10px;
}

#checkout-modal input {
    width: 250px;
}

#checkout-modal button {
    height: 40px;
}

@media only screen and (max-width: 1558px) {
    #app-info {
        margin-top: 30px;
        order: 1;
    }
}