.imgSlider {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    transition: all 0.4s ease;
}

.imgSlider .img-div {
    padding: 0px;
    border-radius: 22px;
    border: 1px solid #838892;
}

.imgSlider img {
    width: 950px;
    border-radius: 20px;
    border: 3px solid #56d8fc;
}

.imgSlider .arrow-icon-div {
    position: relative;
}

.imgSlider .arrow-icon-div > div {
    width: 100px;
}

.imgSlider .arrow-icon {
    color: #04a2da;
    font-size: 60px;
    cursor: pointer;

    position: absolute;
    top: 50%;

    transform: translateY(-50%);
}

.imgSlider .arrow-icon-border {
    color: #838892;
    font-size: 67px;
    cursor: pointer;

    position: absolute;
    top: 50%;

    transform: translateY(-50%);
}

.imgSlider .arrow-icon.left {
    left: 30px;
}

.imgSlider .arrow-icon.right {
    right: 30px;
}

.imgSlider .arrow-icon-border.right {
    right: 26px;
}

.imgSlider .arrow-icon-border.left {
    left: 26px;
}

.imgSlider .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.imgSlider .index-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin: 10px
}

.imgSlider .slider-index {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #838892;
    margin: 5px;
    cursor: pointer;
}

.imgSlider .slider-index.active {
    background: #04a2da;
}

@media only screen and (max-width: 1160px) {
    .imgSlider img {
        width: 600px;
    }
}

@media only screen and (max-width: 860px) {
    .imgSlider .arrow-icon-div {
        display: none;
    }
}

@media only screen and (max-width: 630px) {
    .imgSlider img {
        width: 400px;
    }
}