#contact {
    padding: 50px 20px;
}

#contact .contact-title {
    font-size: 30px;
    user-select: none;
}

#contact > div {
    width: -webkit-fill-available;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    color: #02073E;
    margin: 10px;
}

#contact .social-div {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;

    padding: 20px;

    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;

    background-color: #ffff;
    border: 1px solid #2596be;

    border-radius: 7px;
    margin: 20px;

    cursor: pointer;

    user-select: none;
}

#contact .social-div:hover {
    transform: scale(1.05);
}

#contact .copied {
    position: absolute;
    top: -55px;
    left: 40%;
    transform: translateX(-40%);
}

#contact .social-div > span {
    font-size: 20px;
    font-family: Bree Serif;
    color: #02073E;
    line-height: 1.45;
    font-weight: 500;
    margin-right: 48px;
    -webkit-transition: 500ms;
    transition: 500ms;
    margin: 10px 20px;
}