.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    position: relative;

    border-radius: 7px;
    background-color: #F9FBFD;
    border: 1px solid #2596be;

    padding: 20px;
    margin: 10% auto;
    width: fit-content;

    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}