#header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;

    background-color: #fff;

    width: -webkit-fill-available;

    transition: all 0.4s ease;
    border-bottom: 1px solid #E9EDF5;

    padding: 20px 0px;

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

#header > div {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}

#header #header-links {
    justify-content: flex-start;
    user-select: none;
}

#header #header-logo {
    color: #02073E;
    margin-left: 15px;
    margin-right: 20px;
    cursor: pointer;
    width: -webkit-fill-available;
}

#header #header-links > a,
#header #header-actions > a {
    font-size: 20px;
    color: #02073E;
    font-weight: 400;
    cursor: pointer;
    line-height: 1.2;
    margin-right: 48px;
    -webkit-transition: 500ms;
    transition: 500ms;
    white-space: nowrap;
    margin: 10px 25px;
}

#header #header-links > a:hover {
    color: #2596be;
}

#header #header-links > a.active {
    color: #2596be;
}

@media only screen and (max-width: 800px) {
    #header #header-logo {
        flex: unset;
    }

    #header #header-links {
        flex: unset;
        width: -webkit-fill-available;
        justify-content: space-evenly;
        margin-top: 15px;
    }
}